html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
a {
  text-decoration: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap');

body {
	font-family: 'Open Sans', 'Segoe UI', sans-serif;
  overflow-x: hidden;
}
* {
  scroll-behavior: smooth;
	user-select: none;
}



.home {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .top {
    // background: #F4F1EC;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .titles {
      z-index: 10;
      h1 {
        font-size: 6rem;
        font-weight: 700;
        // color: #c7a05c;
      }
      h4 {
        font-size: 1.15rem;
        // color: #c7a05c;
      }
      @media screen and (max-width: 965px) {
        h1 {
          font-size: 3rem;
        }
        h4 {
          font-size: 1.05rem;
        }
      }
      @media screen and (max-width: 533px) {
        h1 {
          font-size: 2.2rem;
        }
        h4 {
          font-size: 1rem;
        }
      }
    }
    .poseidon-background {
      filter: sepia(15%) opacity(60%);
      position: absolute;
      background: url(.././assets/img/poseidon.png) center/cover;
      width: 100vw;
      height: 100vh;
      @media screen and (max-width: 1200px) {
        background: url(.././assets/img/poseidon.png) left/cover;
      }
      @media screen and (max-width: 533px) {
        background: url(.././assets/img/poseidon-mobile.png) center/cover;
      }
    }
    .arrow {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: 13vh;
      transform: rotate(90deg);
      border-radius: 100px;
      width: 40px;
      height: 40px;
      background: #3333333f;
      color: #eeeeee;
      animation: arrow-bellow 1.5s infinite;
      cursor: pointer;
    }
    @keyframes arrow-bellow {
      0% {
        outline: 1px solid #eeeeee;
        transform: translateY(0px) rotate(90deg);
      }
      50% {
        transform: translateY(10px) rotate(90deg);
      }
      65% {
        outline: 10px solid #eeeeee;
      }
      100% {
        outline: 1px solid #eeeeee;
        transform: translateY(0px) rotate(90deg);
      }
    }
  }

  .bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    button {
      background: rgb(182, 182, 182);
      border: none;
      border-radius: 5px;
      margin-bottom: 20px;
      padding: 7px 10px;
      cursor: pointer;
    }
    .viewer {
      padding: 40px 30%;
      margin: auto;
    }
    @media screen and (max-width: 1350px) {
      .viewer {
        padding: 40px 20%;
      }
    }
    @media screen and (max-width: 835px) {
      .viewer {
        padding: 40px 10%;
      }
    }
    @media screen and (max-width: 500px) {
      .viewer {
        padding: 40px 0;
        .slider-image {
          width: 99vw;
        }
      }
    }
  }
  .slider-container {
    position: relative;
    .slider-button {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      background: #33333325;
      color: #eeeeee;
      border-radius: 100px;
      width: 40px;
      height: 40px;
      border: none;
      cursor: pointer;
      transition: 0.3s;
      &:hover {
        background-color: #33333395;
      }
      &:active {
        width: 45px;
        height: 45px;
        background-color: #333333b4;
      }
    }
    .prev-button {
      left: -40px;
      transform: rotate(180deg);
      top: calc(50% - 20px);
    }
    .next-button {
      right: -40px;
    }
    @media screen and (max-width: 500px) {
      .buttons {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 30px;
        .slider-button {
          display: block;
          position: relative;
          top: 0;
          transform: translateY(0);
          background-color: #33333379;
        }
        .prev-button {
          left: 0;
          transform: rotate(180deg);
          top: 0;
        }
        .next-button {
          right: 0;
        }
      }
    }
    .slider-image {
      width: 100%;
    }
  }
}